import React from 'react'
import styled, { ThemeProvider, injectGlobal } from 'styled-components'
import theme, { inverted } from './theme'
import { A, Button, Label, Head, SubHead, P } from '../components'
import { Grid, Row, Col } from 'react-bootstrap'

injectGlobal`
  @import url('https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css');
  @import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700|Source+Code+Pro:600');
  body {
    margin: 0;
    font-family: 'Open Sans', sans-serif;
    background-color: ${theme.background};
    color: ${theme.main};
    font-size: 13px;
    line-height: 2em;
  }
  a {
    color: ${theme.main};
    text-decoration: underline;
  }
`
const Container = styled.div`
`
const Header = styled((props) => (<Grid><Row><Col md={12} {...props}/></Row></Grid>))`
  margin: 40px 0;
`
const FooterContainer = styled.div`
  padding: 20px 0;
  background-color: ${theme.main};
  color: ${theme.background};
`
const Main = styled(Grid)`
  margin-bottom: 40px;
`
const Footer = styled((props) => (<Grid><Row><Col md={12} {...props}/></Row></Grid>))`
`

export default ({ data, children }) => {
  const { site: { siteMetadata: { title, description, email } }} = data
  return (<ThemeProvider theme={theme}>
    <Container>
      <Header>
        <Head>{title}</Head>
        <P>{description}</P>
      </Header>
      <Main>
        {children()}
      </Main>
      <ThemeProvider theme={inverted}>
        <FooterContainer>
          <Footer>
            mailto: <A href={`mailto:${email}`}>{email}</A>
          </Footer>
        </FooterContainer>
      </ThemeProvider>
    </Container>
  </ThemeProvider>)
}
export const query = graphql`
  query LayoutQuery {
    site {
      siteMetadata {
        title
        description
        email
      }
    }
  }
`



